import { Component, OnInit } from '@angular/core';
import {MusicaService} from '../services/musica.service'
import {IResult} from '../interfaces/iresult';
import { MatBottomSheet } from '@angular/material/bottom-sheet';
import {AlbumComponent} from '../album/album.component';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})

export class HomeComponent implements OnInit {

  searchStr: string;
  searchRes: IResult[];


  constructor(private musicaService: MusicaService, private bottomSheet: MatBottomSheet) { }

  ngOnInit(): void {
  }

  searchMusic(){
    this.musicaService.searchMusic(this.searchStr)
    .subscribe(results => {
      this.searchRes=results;
    });
  }

  openSheet(selectedres: IResult){
    let sheet = this.bottomSheet.open(AlbumComponent, {
      data:{   
        _artist: selectedres.artist.name,
        _track: selectedres.title,
        _album: selectedres.album.title,     
        _preview: selectedres.preview 
      }
    })
    console.log(selectedres);
  }

}